<template>
  <div class="client-info-detail frame-container box">

    <!-- 补白 -->
    <div class="client-info-detail__padding"></div>

    <!-- 导航栏 -->
    <div class="client-info-detail__nav box plr-30 align-hor-bet">
      <div class="icon iconfont  iconreturn"
        @click="onBack">
      </div>
      <div class="title">录入资料</div>
      <div
        class="text"
        style="background-color:#4ba8ff;
            padding:3px;
            color:white;
            border-radius:5px;"
        @click="saveBtn"
      >保存</div>
    </div>
    <!-- 选项卡 -->
    <div  style="" class="client-info__tabs box align-bottom">
      <div
        v-for="(item, index) in tabList"
        :key="index"
        :class="['item', 'box', 'match-left-space', 'align-ver-bottom', index == current ? 'active' : '' ]"
        @click="onTabItemClick(index, item.status)">
        <div>{{item.name}}</div>
        <div class="bar mt-20"></div>
      </div>
    </div>
    <!-- ?提示 -->
    <mu-dialog title="录入提示" width="360" :open.sync="openSimple">
      <div>
        <span style="color: rgb(226, 25, 24);"> * </span>为必填项,进件前必须填
      </div>
      <div>
        <span style="color: rgb(38, 176, 48);"> * </span>为直连通道必填项
      </div>
      <div>
        <span style="color: rgb(226, 25, 24);"></span>非小微商户须填写营业执照
      </div>
      <div style="color: rgb(255, 166, 0);">
        请按提示准确填写信息
      </div>
      <mu-button slot="actions" flat color="primary" @click="closeSimpleDialog">确定</mu-button>
    </mu-dialog>
    <!-- 经营者信息 -->
    <div v-if="current == 0" class="client-info-detail__content">
      <div class="match-width box align-default">
        <div class="title kzky">经营者信息
          <div class="boxTost_icon" @click="openSimpleDialog"></div>
        </div>
        <div class="item">
          <div class="subtitle">
            <span class="star">*</span>经营者身份证正面
          </div>
          <div class="match-left-space align-right">
            <div class="match-left-space" @click="callTakePhoto(2)">&nbsp;</div>
            <div
              class="input align-right"
              style="width: auto">
              <img
                v-if="detail.epresentativePhotoId"
                :src="detail.epresentativePhotoId | loadImage"
                @click="previewImage(detail.epresentativePhotoId)">
              <!-- <div v-else class="img"></div> -->
              <div class="icon iconfont iconenter ml-10" @click="callTakePhoto(2)"></div>
            </div>
          </div>
        </div>
        <div class="item">
          <div class="subtitle">
            <span class="star">*</span>经营者身份证背面
          </div>
          <div class="match-left-space align-right">
            <div class="match-left-space" @click="callTakePhoto(5)">&nbsp;</div>
            <div
              class="input align-right"
              style="width: auto">
              <img
                v-if="detail.epresentativePhotoId2"
                :src="detail.epresentativePhotoId2 | loadImage"
                @click="previewImage(detail.epresentativePhotoId2)">
              <!-- <div v-else class="img"></div> -->
              <div class="icon iconfont iconenter ml-10" @click="callTakePhoto(5)"></div>
            </div>
          </div>
        </div>
        <div class="item">
          <div class="subtitle">
            <span class="star">*</span>经营者/法人姓名
          </div>
          <div class="match-left-space align-right">
            <div class="input align-right">
              <input placeholder="请输入内容" v-model="detail.representativeName"/>
            </div>
          </div>
        </div>
        <div class="item">
          <div class="subtitle">
            <span class="star">*</span>身份证号码
          </div>
          <div class="match-left-space align-right">
            <input placeholder="请输入内容" v-model="detail.certificateNum"/>
          </div>
        </div>
        <div class="item">
          <div class="subtitle">
            <span class="color-l">*</span>证件有效期
          </div>
          <div class="match-left-space align-right">
            <div class="input align-default">
              <mu-date-input
                @change="onDateTimeChange(1, 1, $event)"
                v-model="detail.startCertificateTime"
                label="请输入开始时间"
                container="bottomSheet"
                label-float
                full-width>
              </mu-date-input>
              <mu-date-input
                @change="onDateTimeChange(1, 2, $event)"
                v-model="detail.endCertificateTime"
                label="请输入结束时间"
                container="bottomSheet"
                label-float
                full-width>
              </mu-date-input>
            </div>
            <div class="ft-14" style="color:#FFA600">
              结束时间为长期请选择 1970-01-01
            </div>
          </div>
        </div>
        <div class="item">
          <div class="subtitle">
            <span class="star">*</span>手机号码
          </div>
          <div class="match-left-space align-right">
            <input
              placeholder="请输入手机号码"
              v-model="detail.phone"/>
          </div>
        </div>
        <div class="item">
          <div class="subtitle">
            <span class="star">*</span>常用邮箱
          </div>
          <div class="match-left-space align-right">
            <input
              placeholder="请输入常用邮箱"
              v-model="detail.email"/>
          </div>
        </div>
        <div class="title">营业执照（小微商户请选择商户类型）</div>
        <div class="item">
          <div class="subtitle">
            <span class="color-l">*</span>营业执照照片
          </div>
          <!-- <div class="ft-14">清晰彩色照片or彩色扫描件or复印件</div> -->
          <div class="match-left-space align-right">
            <div class="match-left-space" @click="callTakePhoto(6)">&nbsp;</div>
            <div
              class="input align-right"
              style="width: auto">
              <img
                v-if="detail.businessLicensePhotoId"
                :src="detail.businessLicensePhotoId | loadImage"
                @click="previewImage(detail.businessLicensePhotoId)">
                <div class="icon iconfont iconenter ml-10" @click="callTakePhoto(6)"></div>
            </div>
          </div>
        </div>
        <div @click="comTypeArrBtn" class="item">
          <div class="subtitle">
            <span class="color-l">*</span>商户类型
          </div>
          <div class="match-left-space align-right">
            <div class="match-left-space" >&nbsp;</div>
              <input
                readonly
                placeholder="请选择商户类型"
                v-model="comTypeName"/>
              <div class="icon iconfont iconenter ml-10"></div>
          </div>
        </div>
        <div class="item">
          <div class="subtitle">
            <span class="color-l">*</span>营业执照注册号
          </div>
          <div class="match-left-space align-right">
            <input
              placeholder="请输入注册号"
              v-model="detail.license"/>
          </div>
        </div>
        <div class="item">
          <div class="subtitle">
            <span class="color-l">*</span>商户全称
          </div>
          <div class="match-left-space align-right">
            <input
              placeholder="请填写商户名称"
              v-model="detail.merchantName"/>
          </div>
        </div>
        <div class="ft-14 text-left color-f">商户全称需与营业执照的登记名称一致，不一致将被驳回；若营业执照上商户名称为“空“或“无”时，填写"个体户+经营者姓名"，如“个体户张三”</div>
      </div>
      <div class="match-width box align-center">
        <div
          class="next box align-center"
          @click="onNext(1)">
          下一步
        </div>
      </div>
    </div>
    <div v-if="current == 1" class="client-info-detail__content box match-left-space">
      <div class="match-width box align-default">
        <div class="title">商户信息</div>
        <div class="ft-14 color-yellow text-left">请上传门店照片（门店招牌清晰可见）和门店内部环境照片</div>
        <div class="item">
          <div class="subtitle">
            <span class="star">*</span>门头照片
          </div>
          <div class="match-left-space align-right">
            <div class="match-left-space" @click="callTakePhoto(7)">&nbsp;</div>
            <div
              class="input align-right"
              style="width: auto">
              <img
                v-if="detail.doorPhoto"
                :src="detail.doorPhoto | loadImage"
                @click="previewImage(detail.doorPhoto)">
              <!-- <div v-else class="img"></div> -->
              <div class="icon iconfont iconenter ml-10" @click="callTakePhoto(7)"></div>
            </div>
          </div>
        </div>
        <div class="item">
          <div class="subtitle">
            <span class="star">*</span>店内环境照
          </div>
          <div class="match-left-space align-right">
            <div class="match-left-space" @click="callTakePhoto(8)">&nbsp;</div>
            <div
              class="input align-right"
              style="width: auto">
              <img
                v-if="detail.storePhoto"
                :src="detail.storePhoto | loadImage"
                @click="previewImage(detail.storePhoto)">
              <div class="icon iconfont iconenter ml-10" @click="callTakePhoto(8)"></div>
            </div>
          </div>
        </div>
        <div class="item">
          <div class="subtitle">
            <span class="star">*</span>请填写商户简称
          </div>
          <div class="match-left-space align-right">
            <input
              placeholder="请输入姓名"
              v-model="detail.shortName"/>
          </div>
        </div>
        <div @click="choose1" class="item">
          <div class="subtitle">
            <span class="star">*</span>门店省市区
          </div>
          <div class="match-left-space align-right">
            <div class="ft-14" v-if="detail.storeAddress == ''">请选择省市区</div>
            {{detail.storeAddress}}
            <div class="icon iconfont iconenter ml-10"></div>
          </div>
        </div>
         <!-- 门店省市区 -->
        <p class="pwrap" v-if="show1">
	        <v-distpicker type="mobile" @province="onChangeProvince1" @city="onChangeCity1" @area="onChangeArea1"></v-distpicker>
	      </p>
        <div class="item">
          <div class="subtitle">
            <span class="star">*</span>经营地址
          </div>
          <div class="match-left-space align-right">
            <input
              placeholder="请填写详细的经营场所地址"
              v-model="detail.registerAddress"/>
          </div>
        </div>
        <div class="item">
          <div class="subtitle">
            <span class="star">*</span>客服电话
          </div>
          <div class="match-left-space align-right">
            <input
              placeholder="请填写客服电话"
              v-model="detail.cusServiceTel"/>
          </div>
        </div>
        <div class="ft-14 text-left">客服电话将在微信交易记录中向买家展示，提供咨询服务，手机、400电话、座机（须填写区号）任选一个。</div>
      </div>
      <div class="match-width box align-center">
        <div
          class="next box align-center"
          @click="onNext(2)">
          下一步
        </div>
      </div>
    </div>
    <!-- 结算信息 -->
    <div v-if="current == 2" class="client-info-detail__content box match-left-space">
      <div class="match-width box align-default">
        <div class="title">结算信息</div>
        <div class="item">
          <div class="subtitle">
            <span class="star">*</span>银行卡正面
          </div>
          <div class="match-left-space align-right">
            <div class="match-left-space" @click="callTakePhoto(9)">&nbsp;</div>
            <div
              class="input align-right"
              style="width: auto">
              <img
                v-if="detail.bankCardNumber"
                :src="detail.bankCardNumber | loadImage"
                @click="previewImage(detail.bankCardNumber)">
              <!-- <div v-else class="img"></div> -->
              <div class="icon iconfont iconenter ml-10" @click="callTakePhoto(9)"></div>
            </div>
          </div>
        </div>
        <div class="item">
          <div class="subtitle">
            <span class="star">*</span>账户类型
          </div>
          <div
            class="match-left-space box align-right"
            @click="savAccountType">
            <div
              class="input ellipsis"
              style="text-align: right">
              {{detail.accountType}}
            </div>
            <div class="icon iconfont iconenter ml-10"></div>
          </div>
        </div>
        <div class="item">
          <div class="subtitle">
            <span class="star">*</span>开户名称
          </div>
          <div class="match-left-space align-right">
            <input
              placeholder="请输入开户名称"
              v-model="detail.accountHolder"/>
          </div>
        </div>
          <div class="item" style="border: none">
          <div class="subtitle">
            <span class="star">*</span>开户银行
          </div>
        </div>
        <mu-select label="" v-model="detail.bankName" full-width>
          <mu-option solo=true v-for="(option,index) in array4" :key="index" :label="option" :value="option"></mu-option>
        </mu-select>
        <div class="item">
          <div class="subtitle">
            <span class="star">*</span>银行账号
          </div>
          <div class="match-left-space align-right">
            <input
              placeholder="请输入银行账号并仔细核对"
              v-model="detail.accountNumber"/>
          </div>
        </div>
        <div @click="choose" class="item">
          <div class="subtitle">
            <span class="star">*</span>开户城市
          </div>
          <div class="match-left-space align-right">
            <div class="ft-14" v-if="detail.bankCity == ''">请选择开户城市</div>
            {{detail.bankCity}}
            <div class="icon iconfont iconenter ml-10"></div>
          </div>
        </div>
        <!-- 开户城市 -->
        <p class="pwrap" v-if="show">
	        <v-distpicker type="mobile" @province="onChangeProvince" @city="onChangeCity" @area="onChangeArea"></v-distpicker>
	      </p>
        <div class="item">
          <div class="subtitle">
            <span class="star"></span>开户支行
          </div>
          <div class="match-left-space align-right">
            <input
              placeholder="请输入开户行"
              v-model="detail.bankOutlet"/>
          </div>
        </div>
        <div class="ft-14 text-left color-yellow">
          如开户银行选择其他银行，或间连通道的对公账户，需填写银行全称，如"深圳农村商业银行XXX支行"
        </div>
      </div>
      <div class="match-width box align-center">
        <div
          class="next box align-center"
          @click="saveBtn">
          保存
        </div>
      </div>
    </div>
    <!-- action-sheet -->
      <mu-bottom-sheet :open.sync="open">
        <div class="action-sheet box align-default">

          <div class="title box align-hor-bet plr-30">
            <div
              @click="onActionSheetClose">
              返回
            </div>
            <div
              class="confirm"
              @click="onActionSheetConfirm">
              确定
            </div>
          </div>

          <!-- 销售商品场景 -->
          <div
            class="match-width"
            v-for="(item, index) in scenes"
            :key="index"
            v-if="status == 1">
            <div
              :class="['item align-hor-bet plr-30 ptb-30', item.checked?'active':'']"
              @click="changeItem(index)">
              <div>{{item.name}}</div>
              <div
                v-if="item.checked"
                class="icon iconfont iconcheck">
              </div>
              <div class="pass" v-else></div>
            </div>
          </div>

          <!-- APP上线状态 -->
          <div
            class="match-width"
            v-for="(item, index) in appStates"
            :key="index"
            v-if="status == 3">
            <div
              :class="['item align-hor-bet plr-30 ptb-30', (curAppState == index)?'active':'']"
              @click="changeItem(index)">
              <div>{{item}}</div>
              <div
                v-if="(curAppState == index)"
                class="icon iconfont iconcheck">
              </div>
              <div class="pass" v-else></div>
            </div>
          </div>

          <!-- 补脚 -->
          <div class="action-sheet__padding"></div>

        </div>
      </mu-bottom-sheet>

      

      <mu-bottom-sheet :open.sync="openThree">
        <div class="action-sheet box align-default">

          <div class="title box align-hor-bet plr-30">
            <div
              @click="onBackStep">
              返回
            </div>
            <div class="confirm">
              <div v-if="isEnd" @click="onActionSheetConfirm(2)">确定</div>
              <div v-else @click="onNextStep">取消</div>
            </div>
          </div>
        </div>
        <div class="action-sheet__header align-left box plr-30">
          请选择：{{threeList | threeListFilter}}
        </div>
        <div class="action-sheet__content">
          <div
            class="match-width"
            v-for="(item, index) in tmpSourceData"
            :key="index">
            <div
              :class="['item align-hor-bet plr-30 ptb-30', (item.value === threeList[curThree])?'active':'']"
              @click="changeItem(index, item.value)">
              <div>{{item.value}}</div>
              <div
                v-if="item.value === threeList[curThree]"
                class="icon iconfont iconcheck">
              </div>
              <div class="pass" v-else></div>
            </div>
          </div>
        </div>
      </mu-bottom-sheet>

      <!-- 隐藏获取图片 -->
      <div class="file">
        <input type="file" ref="fileCamera" capture="camera" accept="image/*" @change="onFileChange"/>
        <div class="cover"></div>
      </div>
      <!-- 隐藏获取图片 -->
      <div class="file">
        <input type="file" ref="file" accept="image/*" @change="onFileChange"/>
        <div class="cover"></div>
      </div>
    <!-- 图片预览 -->
    <div class="cover-x align-default" v-if="isImagePreview" @click="isImagePreview = false">
      <div class="match-left-space"></div>
      <div class="image-preview">
        <img class="match-parent" :src="imgUrl | previewLoadImage">
      </div>
      <div class="match-left-space"></div>
    </div>
    <!-- action-sheet -->
    <mu-bottom-sheet :open.sync="openMenu">
      <div class="action-sheet box align-default menu">

        <div
          class="item align-center"
          @click="onTakePhoto(1)">
          拍照
        </div>

        <div
          class="item align-center"
          @click="onTakePhoto(2)">
          从手机相册选择
        </div>

        <div
          class="item align-center cancel"
          @click="onActionMenuShow(2)">
          取消
        </div>

        <!-- 补脚 -->
        <div class="action-sheet__padding"></div>

      </div>
    </mu-bottom-sheet>
    <!-- 选择商户类型 -->
    <mu-bottom-sheet :open.sync="comType">
      <div class="action-sheet box align-default menu">
        <div
          v-for="(item, i) in comTypeArr"
          :key="i"
          class="item align-center"
          @click="savcomTypeBtn(item)">
          {{item.name}}
        </div>
        <!-- 补脚 -->
        <div class="action-sheet__padding"></div>

      </div>
    </mu-bottom-sheet>
     <!--  -->
    <mu-bottom-sheet :open.sync="selType">
      <div class="action-sheet box align-default menu">
        <div
          v-for="(item, i) in accountTypeArr"
          :key="i"
          class="item align-center"
          @click="accountTypeBtn(item)">
          {{item}}
        </div>
        <!-- 补脚 -->
        <div class="action-sheet__padding"></div>

      </div>
    </mu-bottom-sheet>
    <!-- 返回 -->
    <mu-dialog title="是否保存？" width="600" max-width="80%" :esc-press-close="false" :overlay-close="false" :open.sync="goOpenAlert">
      是否保存已填写信息
      <mu-button slot="actions" flat color="" @click="goCloseAlertDialog(0)">不保存</mu-button>
      <mu-button slot="actions" flat color="primary" @click="goCloseAlertDialog(1)">保存</mu-button>
    </mu-dialog>
  </div>
</template>

<script>
import { url } from '@/utils/src/request'
import { CLIENT_INFO_DETAIL, CLIENT_INFO_RECORD_NEXT } from '@/router/types'
import { clientInfoDetailIDLocal, clientInfoDetailLocal, clientInfoDetailMerchantNameLocal } from '@/storage'
import { clientInfoApi } from '@/api'
import { getCateGory } from '@/api/businessCategory'
import VDistpicker from 'v-distpicker'
// 商户类型小微（SUBJECT_TYPE_XIAOWEI）个体工商户（SUBJECT_TYPE_INDIVIDUAL）企业（SUBJECT_TYPE_ENTERPRISE）
export default {
  name: 'getAddress',
  components: { VDistpicker },
  data() {
    return {
      openSimple: false,
      accountType: false,
      accountTypeArr: [
        '法人账户',
        '对公账户'
      ],
      comType: false,
      comTypeName: '',
      comTypeArr: [
        {
          id: 'SUBJECT_TYPE_INDIVIDUAL',
          name: '个体工商户'
        },
        {
          id: 'SUBJECT_TYPE_ENTERPRISE',
          name: '企业'
        },
        {
          id: 'SUBJECT_TYPE_XIAOWEI',
          name: '商户类型小微'
        }
      ],
      show:false,
      show1:false,
      txt1:'',
      txt2:'',
      txt3:'',   
      selyh: false,
      array4: [
        '工商银行',
        '交通银行',
        '招商银行',
        '民生银行',
        '中信银行',
        '浦发银行',
        '兴业银行',
        '光大银行',
        '广发银行',
        '平安银行',
        '北京银行',
        '华夏银行',
        '农业银行',
        '建设银行',
        '邮政储蓄银行',
        '中国银行',
        '宁波银行',
        '其他银行',
        '中国农业发展银行',
        '浙商银行',
        '渤海银行',
        '农村商业银行',
        '城市商业银行',
        '村镇银行',
        '其他银行907'
      ],
      selType: false,
      current: 0,
       tabList: [
        {
          name: '联系信息',
          status: 1
        },
        {
          name: '商户信息',
          status: 2
        },
        {
          name: '结算信息',
          status: 3
        }
      ],
      detail: {},
      open: false,
      status: 0,
      scenes: [
        {
          id: 1,
          name: '个体工商户',
          checked: false
        },
        {
          id: 2,
          name: '企业',
          checked: false
        },
        {
          id: 3,
          name: '小微商户',
          checked: false
        }
      ],
      sellCheck: [],
      appStates: ['已上线', '未上线'],
      curAppState: 0,
      openMenu: false,
      photoTaker: 0,
      isImagePreview: false,
      imgUrl: '',
      openThree: false,
      threeList: [],
      curThree: 0,
      sourceData: [],
      tmpSourceData: [],
      isEnd: false,
      merchantName: '',
      goOpenAlert: false,
    }
  },
  components: {
  },
  created() {
    
  },
  methods: {
    goCloseAlertDialog (e) {
      if( e === 1 ) {
        this.goOpenAlert = false;
        clientInfoApi.merchantAdd(this.detail).then( res => {
          console.log(res)
          if( res.code === 200 ) {
            this.$toast.success(res.msg);
            this.$router.push({
              name: CLIENT_INFO_DETAIL
            })
          }else {
            this.$toast.info(res.msg);
          }
        }, (err) => {
          this.$toast.success(err.msg);
        })        
      }else {
        this.$router.push({
          name: CLIENT_INFO_DETAIL
        })
      }
    },
    openSimpleDialog () {
      this.openSimple = true;
    },
    closeSimpleDialog () {
      this.openSimple = false;
    },
    //开户银行城市选择器
    choose(){
      this.show=!this.show
    },
    //门店省市区城市选择器
    choose1(){
      this.show1=!this.show1
      console.log(this.show1)
    },
    onChangeProvince(a){
      console.log(a) 
      this.txt1 = a.value + '/'
      this.detail.businessLevOne = a.value;
    },    
    onChangeCity(a){
      console.log(a)    
      this.txt2 = a.value + '/'   
      this.detail.businessLevTwo = a.value; 
    },
    onChangeArea(a){
      console.log(a)  
      this.txt3 = a.value
      this.detail.bankCity = this.txt1 + this.txt2 + this.txt3;
      this.show=false
    }, 
     onChangeProvince1(a){
      console.log(a) 
      this.txt1 = a.value + '/'
    },    
    onChangeCity1(a){
      console.log(a)    
      this.txt2 = a.value + '/'   
    },
    onChangeArea1(a){
      console.log(a)  
      this.txt3 = a.value
      this.detail.storeAddress = this.txt1 + this.txt2 + this.txt3;
      this.show=false
    },      

    savAccountType() {
      this.selType = true
    },
    //账户类型
    accountTypeBtn(e) {
      this.detail.accountType = e;
      this.selType = false
    },
    //comTypeArrBtn
    comTypeArrBtn(e) {
      console.log(e)
      this.comType = true
    },
    savcomTypeBtn(e) {
      this.detail.subjectType = e.id
      this.comTypeName = e.name
      this.comType = false
    },
    //保存按钮
    saveBtn() {
      console.log(this.detail)
      this.merchantAdd()
    },
    merchantAdd() {
      clientInfoApi.merchantAdd(this.detail).then( res => {
        console.log(res)
        if( res.code === 200 ) {
          this.$toast.success(res.msg);
        }else {
          this.$toast.info(res.msg);
        }
      }, (err) => {
        this.$toast.success(err.msg);
      })
    },
     // 选项卡切换
    onTabItemClick (index, status) {
      this.current = +index
    },
    // 返回
    onBack () {
      this.goOpenAlert = true;
    },
     // 时间戳
    getTimestamp (str) {
      return str ? (new Date(Date.parse(str)).getTime()) : 0
    },
    // 时间改变
    onDateTimeChange (status, index, e) {
      //    status         index
      // 1  证件有效期      开始时间
      // 2  营业期限        结束时间
      if (+status === 1) {
        let startTime = this.detail.startCertificateTime
        let endTime = this.detail.endCertificateTime
        if (startTime && endTime) {
          startTime = this.getTimestamp(startTime)
          endTime = this.getTimestamp(endTime)
          if (startTime >= endTime) {
            this.$toast.error('开始时间必须早于结束时间')
            this.detail.startCertificateTime = ''
            this.detail.endCertificateTime = ''
          } else {
            this.detail.startCertificateTime = this.getTime(this.detail.startCertificateTime)
            this.detail.endCertificateTime = this.getTime(this.detail.endCertificateTime)
          }
        }
      } else if (+status === 2) {
        let startTime = this.detail.startBusinessTime
        let endTime = this.detail.endBusinessTime
        if (startTime && endTime) {
          startTime = this.getTimestamp(startTime)
          endTime = this.getTimestamp(endTime)
          // console.log(startTime >= endTime)
          if (startTime >= endTime) {
            this.$toast.error('开始时间必须早于结束时间')
            this.detail.startBusinessTime = ''
            this.detail.endBusinessTime = ''
          } else {
            this.detail.startBusinessTime = this.getTime(this.detail.startBusinessTime)
            this.detail.endBusinessTime = this.getTime(this.detail.endBusinessTime)
          }
        }
      }
    },
    // 获取年月日
    getTime (str) {
      if (str) {
        // let tmp = new Date(Date.parse(str)).toLocaleDateString()
        let date = new Date(Date.parse(str))
        let y = date.getFullYear()
        let m = date.getMonth() + 1
        if (m < 10) {
          m = '0' + m
        }
        let d = date.getDate()
        if (d < 10) {
          d = '0' + d
        }
        // tmp = tmp.split('/').join('-')
        let tmp = y + '-' + m + '-' + d
        return tmp
      }
      return ''
    },
    // 唤醒action-sheet
    // status
    // 1  销售商品场景
    // 2  经营类目
    // 3  app状态
    // 4  账户类型
    callActionSheet (status) {
      this.status = +status
      if (this.status === 3) {
        this.curAppState = this.transferAppStatus(this.detail.appStatus)
        this.open = true
      } else if (this.status === 1) {
        this.open = true
      } else if (this.status === 2) {
        this.openThree = true
        this.curThree = 0
        this.tmpSourceData = this.sourceData
        this.threeList = []
        this.threeList.push(this.sourceData[0].value)
        this.isEnd = false
      }else if (this.status === 4) {
        this.selType = true
      }else if (this.status === 5) {
        console.log(+status)
        this.selyh = true
      }
    },
    // action-sheet取消
    onActionSheetClose () {
      this.open = false
    },
    // action-sheet确认
    onActionSheetConfirm () {
      if (this.status === 1) {
        // 销售商品场景
        // console.log(this.sellCheck)
        if (Array.isArray(this.sellCheck)) {
          if (this.sellCheck.length) {
            this.detail.qualificationType = JSON.stringify(this.sellCheck)
          } else {
            this.detail.sellCheck = ''
          }
        }
        this.open = false
        // console.log(this.detail.sellCheck)
      } else if (this.status === 2) {
        // 经营类目
        this.detail.businessLevOne = this.threeList[0]
        this.detail.businessLevTwo = this.threeList[1]
        this.detail.businessLevThree = this.threeList[2]
        this.openThree = false
      } else if (this.status === 3) {
        // app状态
        if (this.curAppState === 0) {
          this.detail.appStatus = 1
        } else {
          this.detail.appStatus = 2
        }
        this.open = false
      }
    },
    // 切换选项
    changeItem (index, value) {
      if (this.status === 1) {
        // console.log(+index)
        // 销售商品场景
        this.scenes[+index].checked = !this.scenes[+index].checked
        this.sellCheck = this.scenes.map((item) => {
          return (item.checked ? (item.id + '') : '')
        }).filter(item => {
          return (item !== '')
        })
      } else if (this.status === 2) {
        // 经营类目
        // console.log(this.curThree)
        // this.threeList[this.curThree] = value
        this.$set(this.threeList, this.curThree, value)
        // console.log(this.threeList)
        let tmp = this.tmpSourceData.filter((item) => {
          return item.value === value
        })[0].children
        // console.log(this.threeList)
        if (tmp.length) {
          this.curThree++
          this.tmpSourceData = tmp
          this.threeList.push(this.tmpSourceData[0].value)
          if (this.threeList.length > 2) {
            this.isEnd = true
          }
        } else {
          this.isEnd = true
        }
      } else if (this.status === 3) {
        // app状态
        this.curAppState = +index
      }
    },
    // 控制actionSheetMenu显隐
    onActionMenuShow (status) {
      if (+status === 2) {
        this.openMenu = false
      } else if (+status === 1) {
        this.openMenu = true
      }
    },
    // 相机打开
    onTakePhoto (status) {
      if (+status === 1) {
        this.$refs.fileCamera.click()
        // 拍照
      } else if (+status === 2) {
        this.$refs.file.click()
        // 从相机打开
      }
      this.openMenu = false
    },
    // 获取图片
    callTakePhoto (index) {
      // index
      // 1 app
      // 2 公众号
      // 3 特殊资质
      // 4 补充材料
      this.photoTaker = +index
      this.openMenu = true
    },
    // 下一步
    onNext (e) {
      this.current = e
    },
    // 文件改变
    onFileChange (e) {
      if (!e.target.files) {
        return
      }
      let file = e.target.files[0]
      // let file = this.$refs.file.files[0]
      if (file) {
        // console.log(e)
        clientInfoApi.uploadImage(file).then(res => {
          if (res.code === 200) {
            this.$toast.success('图片上传成功')
            let photoId = res.obj
            // console.log(photoId)
            // 1 app
            // 2 公众号
            // 3 特殊资质
            // 4 补充材料
            let params = {}  
            if (this.photoTaker === 1) {
              this.$set(this.detail, 'appPhotoId', photoId)
            } else if (this.photoTaker === 2) {
              this.$set(this.detail, 'epresentativePhotoId', photoId)
              //身份证正面
              params = {
                type: 1,
                id: photoId
              }
              clientInfoApi.cardOcr(params).then( res => {
                let obj = res.obj
                if( res.code === 200 ) {
                  obj.certificateNum ? this.detail.certificateNum =  obj.certificateNum : '';
                  obj.representativeName ? this.detail.representativeName = obj.representativeName : '';
                }
              })
            } else if(this.photoTaker === 5) {
              this.$set(this.detail, 'epresentativePhotoId2', photoId)
              //身份证背面
              params = {
                type: 2,
                id: photoId
              }
              clientInfoApi.cardOcr(params).then( res => {
                let obj = res.obj
                console.log('obj',obj)
                if( res.code === 200 ) {
                  if( obj.startCertificateTime != null ) {
                    let str = obj.startCertificateTime
                    this.detail.startCertificateTime = str.substr(0,4)+'-'+str.substr(4,2)+'-'+str.substr(6,2)
                  }
                  //结束时间为长期 日期为 1970-01-01
                  if( obj.endCertificateTime == '长期' ) {
                    this.detail.endCertificateTime = '1970-01-01'
                  }else if( obj.endCertificateTime != null ) {
                    let str = obj.endCertificateTime
                    this.detail.endCertificateTime = str.substr(0,4)+'-'+str.substr(4,2)+'-'+str.substr(6,2)
                  }
                }
              } )
            }else if(this.photoTaker === 6) {
              this.$set(this.detail, 'businessLicensePhotoId', photoId)
              //营业执照
              params = {
                id: photoId
              }
              clientInfoApi.licenseOcr(params).then( res => {
                console.log('ocr1',res)
                let obj = res.obj
                if( res.code === 200 ) {
                  obj.license ? this.detail.license =  obj.license : '';
                  obj.merchantName ? this.detail.merchantName = obj.merchantName : '';
                  obj.registerAddress ? this.detail.registerAddress =  obj.registerAddress : '';
                }
              })
            }else if (this.photoTaker === 3) {
              this.$set(this.detail, 'specialQualificationPhotoId', photoId)
            } else if (this.photoTaker === 4) {
              this.$set(this.detail, 'supplementPhotoId', photoId)
            }else if (this.photoTaker === 7) {
              this.$set(this.detail, 'doorPhoto', photoId)
            }else if (this.photoTaker === 8) {
              this.$set(this.detail, 'storePhoto', photoId)
            }else if (this.photoTaker === 9) {
              this.$set(this.detail, 'bankCardNumber', photoId)
              params = {
                id: photoId
              }
              clientInfoApi.bankOcr(params).then( res => {
                let obj = res.obj
                if( res.code === 200 ) {
                  obj.bankName ? this.detail.bankName =  obj.bankName : '';
                  obj.cardNum ? this.detail.accountNumber = obj.cardNum : '';
                }
              })
            }
          } else {
            this.$toast.error(res.msg)
          }
        }, (err) => {
          this.$toast.error(err.msg)
        })
      }
    },
    // 获取图片
    getImage (id) {
      return id ? url + `/fms/upload/resource/thumbnail/${id}` : ''
    },
    // 初始化detail对象
    initDetail () {
      this.detail = JSON.parse(clientInfoDetailLocal.get())
      this.detail.subjectType == 'SUBJECT_TYPE_INDIVIDUAL' ? this.comTypeName = '个体工商户' : this.detail.subjectType == 'SUBJECT_TYPE_ENTERPRISE' ? this.comTypeName = '企业' : '商户类型小微';
      this.merchantName = JSON.parse(clientInfoDetailMerchantNameLocal.get())
      this.merchantName = this.merchantName.merchantName
    },
    // 预览图片
    previewImage (id) {
      // console.log(id)
      if (id) {
        this.imgUrl = id
        this.isImagePreview = true
      }
    },
    // 转化app status
    transferAppStatus (status) {
      return (status === 1 || status === '1') ? 0 : 1
    },
    // 转化销售商品场景
    transferScenes (sellCheck) {
      if (!sellCheck) {
        return
      }
      sellCheck = JSON.parse(sellCheck)
      this.scenes = this.scenes.map((item) => {
        if (sellCheck.indexOf((item.id + '')) !== -1) {
          item.checked = true
        } else {
          item.checked = false
        }
        return item
      })
      return this.scenes
    },
    // 下一步
    onNextStep () {
      this.openThree = false
    },
    // 返回
    onBackStep () {
      if (this.curThree > 0) {
        this.curThree--
        this.isEnd = false
      }
      // console.log(this.threeList)
      if (this.threeList.length > 3) {
        return
      }
      if (this.threeList.length > 1) {
        this.threeList.pop()
      }
      if (this.threeList.length <= 1) {
        this.tmpSourceData = this.sourceData
      } else {
        this.tmpSourceData = this.sourceData.filter((item) => {
          return item.value === this.threeList[0]
        })[0].children
      }
    }
  },

  filters: {
    // 获取图片
    loadImage (id) {
      return id ? (url + `/fms/upload/resource/thumbnail/${id}`) : ''
    },
    previewLoadImage (id) {
      return id ? url + `/fms/upload/resource/${id}` : ''
    },
    // app状态过滤
    appStatusFilter (index) {
      return (index === 1 ? '已上线' : '未上线')
    },
    // 经营类目
    businessLevFilter (businessLev) {
      if (!businessLev) {
        return ''
      }
      return ('>' + businessLev)
    },
    // 销售商品场景
    sellCheckFilter (sellCheck) {
      // console.log(sellCheck, typeof sellCheck)
      if (!sellCheck) {
        return ''
      }
      if (typeof sellCheck === 'string') {
        sellCheck = JSON.parse(sellCheck)
      }
      let scenes = ['线下', '公众号/小程序', '网站', 'APP']
      // console.log(66, sellCheck)
      sellCheck = sellCheck.map(x => {
        return scenes[(x - 1)]
      })
      let tmpStr = sellCheck.join('、')
      return tmpStr
    },
    // 三级目录
    threeListFilter (arr) {
      if (Array.isArray(arr) && arr.length) {
        return arr.join(' > ')
      }
      return ''
    }
  },

  mounted () {
    this.id = JSON.parse(clientInfoDetailIDLocal.get())
    // console.log('id 1', this.id)
    if (this.id) {
      this.initDetail()
      this.sourceData = getCateGory()
      this.tmpSourceData = this.sourceData
    } else {
      this.$toast.error('详情数据丢失')
    }
  }
}
</script>
<style scoped>
  .text-left {
    text-align: left !important;
    padding: 10px 10px 10px 18px;
  }
  .color-l {
    color:rgb(6, 164, 18)
  }
  .color-yellow {
    color: rgb(255, 166, 0);
  }
  .color-000 {
    color: black;
  }
  .ft-14 {
    font-size: 14px;
  }
  
  .boxTost_icon{
    width: 46px;
    height: 46px;
    margin-right: 6px;
    background: url('../../assets/images/prompt.png') no-repeat;
    background-size: 40%;
    background-position: center;
  }
  .kzky {
    display: flex;
    justify-content: space-between;
  }
  
</style>



